import {
  ChangeDetectorRef,
  Component,
  inject,
  OnDestroy,
  OnInit,
  signal,
  output,
  ViewChild,
  TemplateRef,
} from '@angular/core';
import { Subject, debounceTime, takeUntil } from 'rxjs';
import { DialogModule } from 'primeng/dialog';
import { TableLazyLoadEvent, TableModule } from 'primeng/table';
import { InputIcon } from 'primeng/inputicon';
import { IconField } from 'primeng/iconfield';
import { InputTextModule } from 'primeng/inputtext';

import {
  ClientListItem,
  ClientResponse,
  ClientsService,
} from '../../../../api';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { FormsModule } from '@angular/forms';
import { ButtonModule } from 'primeng/button';
import { UtilityService } from '../../../../core/services/utility.service';
import { ClientDataService } from '../../services/client-data.service';
import { FilterMetadata } from 'primeng/api';
import { DEFAULT_PAGE_SIZE } from '../../../../core/models';
import { GridComponent } from '../../../../shared/components/grid/grid.component';
import { ApiUrlService } from '../../../../core/services/api-url.service';

import { BroadcastMessageService } from '../../../../core/services/message.service';
import { ActionType } from '../../../../core/models/message.model';
import { hasProperty, isDefined, isString } from '../../../../shared/utils';
import { GridColumn } from '../../../../shared/models';

@Component({
  selector: 'app-client-switcher',
  imports: [
    DialogModule,
    TableModule,
    FormsModule,
    InputIcon,
    IconField,
    InputTextModule,
    ButtonModule,
    GridComponent,
  ],
  templateUrl: './client-switcher.component.html',
  styleUrls: ['./client-switcher.component.scss'],
})
export class ClientSwitcherComponent implements OnInit, OnDestroy {
  private readonly messageService = inject(BroadcastMessageService);

  readonly onClosed = output<void>();

  private readonly DEBOUNCE_TIME = 300;

  @ViewChild('logoTemplate', { static: true }) logoTemplate:
    | TemplateRef<unknown>
    | undefined;

  authDataService = inject(AuthDataService);
  utilityService = inject(UtilityService);
  clientDataService = inject(ClientDataService);
  clientsService = inject(ClientsService);
  cdr = inject(ChangeDetectorRef);
  apiUrlService = inject(ApiUrlService);

  display = signal<boolean>(true);
  searchTerm = signal<string>('');
  clients = signal<ClientListItem[]>([]);
  totalRecords = signal<number>(0);
  selectedClient = signal<ClientResponse | null>(null);
  loading = signal<boolean>(false);
  columns: GridColumn[] = [];

  private readonly searchSubject = new Subject<string>();
  private readonly destroy$ = new Subject<void>();

  ngOnInit(): void {
    this.columns = [
      { field: 'logo', header: 'Logo', cellTemplate: this.logoTemplate },
      { field: 'name', header: 'Name' },
    ];
    this.searchSubject
      .pipe(debounceTime(this.DEBOUNCE_TIME), takeUntil(this.destroy$))
      .subscribe(() => {
        this.loadClients({ first: 0, rows: DEFAULT_PAGE_SIZE });
      });
  }

  ngOnDestroy(): void {
    this.destroy$.next();
    this.destroy$.complete();
  }

  onSearch(): void {
    this.searchSubject.next(this.searchTerm());
  }

  loadClients(event: TableLazyLoadEvent): void {
    const userProfile = this.authDataService.getUserProfile();
    if (!isDefined(userProfile?.owner?.id)) {
      console.warn('Owner ID not found');
      return;
    }

    const ownerId = userProfile.owner.id;
    const filters: Record<
      string,
      FilterMetadata | FilterMetadata[] | string | undefined
    > = {
      ...event.filters,
      state: {
        value: 'active',
        operator: '=',
      },
    };

    const searchTerm = this.searchTerm();
    if (isString(searchTerm) && searchTerm.length > 0) {
      filters['name'] = {
        value: `*${searchTerm}*`,
        operator: '=',
      };
    }

    this.loading.set(true);

    const rqlFilter = this.utilityService.constructRqlFilter(filters);
    const pageSize = isDefined(event.rows) ? event.rows : DEFAULT_PAGE_SIZE;

    this.clientsService
      .listClients(ownerId, event.first, pageSize, 'name:asc', rqlFilter)
      .subscribe({
        next: (data) => {
          if (
            isDefined(data) &&
            hasProperty(data, 'items') &&
            hasProperty(data, 'count')
          ) {
            this.clients.set(data.items);
            this.totalRecords.set(data.count);
          } else {
            console.warn('Invalid response data structure');
          }
          this.loading.set(false);
          this.cdr.detectChanges();
        },
        error: (error) => {
          console.error('Failed to load clients:', error);
          this.loading.set(false);
        },
      });
  }

  selectClient(client: ClientResponse): void {
    //broadcast profile_changed event to flutter
    this.messageService.sendAction(ActionType.profile_changed, {
      clientId: client.id,
    });
    this.clientsService.getClient(client.id).subscribe({
      next: (response: ClientResponse) => {
        this.clientDataService.setSelectedClient(response);
      },
    });

    this.closeDialog();
  }

  closeDialog(): void {
    this.display.set(false);
    this.onClosed.emit();
  }

  openDialog(): void {
    this.display.set(true);
    this.loadClients({
      first: 0,
      rows: DEFAULT_PAGE_SIZE,
      globalFilter: this.searchTerm(),
    });
  }

  getImageUrl(clientId: string): string {
    return `${this.apiUrlService.getCurrentApiUrl()}/accounts/clients/${clientId}/logo`;
  }
}
