import {
  Component,
  DestroyRef,
  inject,
  OnInit,
  OnDestroy,
  signal,
} from '@angular/core';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { firstValueFrom, catchError, of, filter, map } from 'rxjs';
import {
  ActivatedRoute,
  NavigationEnd,
  Router,
  RouterModule,
} from '@angular/router';
import { SplitterModule } from 'primeng/splitter';
import { ScrollPanelModule } from 'primeng/scrollpanel';
import { MessageService } from 'primeng/api';

import { SidebarComponent } from '../sidebar/sidebar.component';
import { AuthDataService } from '../../../core/services/auth-data.service';
import { ThemeComponent } from '../../../shared/components/theme/theme.component';
import {
  AccountsService,
  ClientResponse,
  UserProfileResponse,
} from '../../../api';
import { LocalStorageService } from '../../../shared/services/local-storage.service';
import { LOCAL_STORAGE_KEY_SELECTED_CLIENT } from '../../../core/models';
import { ClientDataService } from '../../../features/clients/services/client-data.service';
import { FlutterLayoutComponent } from '../flutter-layout/flutter-layout.component';
import { ProgressSpinnerModule } from 'primeng/progressspinner';
import { CommonModule } from '@angular/common';
import { ActionType } from '../../../core/models/message.model';
import { BroadcastMessageService } from '../../../core/services/message.service';
import { hasProperty, isDefined, isString } from '../../../shared/utils';
import { MeasurementDateTimeService } from '../../../shared/services/measurement-units/measurement-date-time.service';

@Component({
  selector: 'app-main-layout',
  imports: [
    RouterModule,
    SplitterModule,
    SidebarComponent,
    ScrollPanelModule,
    ThemeComponent,
    FlutterLayoutComponent,
    ProgressSpinnerModule,
    CommonModule,
  ],
  templateUrl: './main-layout.component.html',
  styleUrls: ['./main-layout.component.scss'],
})
export class MainLayoutComponent implements OnInit, OnDestroy {
  private readonly authDataService = inject(AuthDataService);
  private readonly accountsService = inject(AccountsService);
  private readonly localStorageService = inject(LocalStorageService);
  private readonly clientDataService = inject(ClientDataService);
  private readonly route = inject(ActivatedRoute);
  private readonly messageService = inject(MessageService);
  private readonly destroyRef = inject(DestroyRef);
  private readonly router = inject(Router);
  private readonly activatedRoute = inject(ActivatedRoute);
  private readonly broadcastService = inject(BroadcastMessageService);
  private readonly measurementDateTimeService = inject(
    MeasurementDateTimeService,
  );
  readonly isFlutterRoute = signal(false);
  readonly isIframeRoute = signal(false);
  readonly iframeUrl = signal<string | null>(null);

  constructor() {}

  ngOnInit(): void {
    this.initializeUserAndClient();
    this.checkRouteData();
    this.setupRouteHandler();
    this.setupViewportHeight();

    this.router.events
      .pipe(
        filter((event) => event instanceof NavigationEnd),
        map(() => this.checkRouteData()),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe();
  }

  private checkRouteData(): boolean {
    let route = this.activatedRoute;
    while (route.firstChild) {
      route = route.firstChild;
    }

    const routeData = route.snapshot.data;
    const isFlutterRoute = routeData['isFlutterRoute'] ?? false;
    const isIframeRoute = routeData['isIframeRoute'] ?? false;
    const iframeUrl = routeData['iframeUrl'] ?? null;

    this.isFlutterRoute.set(isFlutterRoute);
    this.isIframeRoute.set(isIframeRoute);
    this.iframeUrl.set(iframeUrl);

    return isFlutterRoute;
  }

  private initializeUserAndClient() {
    this.route.data.pipe(takeUntilDestroyed(this.destroyRef)).subscribe({
      next: (data) => {
        const userProfile: UserProfileResponse = data['userProfile'];
        void this.authDataService.setUserProfile(userProfile);
        this.measurementDateTimeService.setTimezoneId(
          userProfile.timeZoneId ?? 'UTC',
        );
        this.initializeClient().catch(() => {});
      },
      error: () => {
        this.messageService.add({
          severity: 'error',
          summary: 'Error',
          detail: 'Failed to load user profile',
        });
      },
    });
  }

  private async fetchClient(clientId: string): Promise<ClientResponse | null> {
    return firstValueFrom(
      this.accountsService.getClient(clientId).pipe(catchError(() => of(null))),
    );
  }

  private async initializeClient(): Promise<void> {
    const savedState = this.localStorageService.getItem<unknown>(
      LOCAL_STORAGE_KEY_SELECTED_CLIENT,
    );

    const savedClientId =
      hasProperty(savedState, 'id') && isString(savedState.id)
        ? savedState.id
        : undefined;

    const defaultClient = this.authDataService.getUserProfile()?.defaultClient;
    const defaultClientId =
      isDefined(defaultClient) && hasProperty(defaultClient, 'id')
        ? defaultClient.id
        : undefined;

    if (!isDefined(savedClientId) && !isDefined(defaultClientId)) {
      this.messageService.add({
        severity: 'warn',
        summary: 'Warning',
        detail: 'No client found for the user',
      });
      return;
    }

    try {
      const client = await this.tryLoadClient(savedClientId, defaultClientId);
      if (!isDefined(client)) {
        this.messageService.add({
          severity: 'warn',
          summary: 'Warning',
          detail: 'No client found for the user',
        });
      }
    } catch (error: unknown) {
      this.messageService.add({
        severity: 'error',
        summary: 'Error',
        detail: `An error occurred while initializing the client:  ${error instanceof Error ? error.message : 'Unknown error'}`,
      });
    } finally {
      this.clientDataService.markInitializationComplete();
    }
  }

  private async tryLoadClient(
    savedClientId?: string,
    defaultClientId?: string,
  ): Promise<ClientResponse | null> {
    if (isDefined(savedClientId) && isString(savedClientId)) {
      const savedClient = await this.fetchClient(savedClientId);
      if (isDefined(savedClient)) {
        this.clientDataService.setSelectedClient(savedClient);
        return savedClient;
      }
    }

    if (isDefined(defaultClientId) && isString(defaultClientId)) {
      const defaultClient = await this.fetchClient(defaultClientId);
      if (isDefined(defaultClient)) {
        this.clientDataService.setSelectedClient(defaultClient);
        return defaultClient;
      }
    }

    return null;
  }

  private setupViewportHeight(): void {
    const setViewportHeight = () => {
      const vh = window.innerHeight * 0.01;
      document.documentElement.style.setProperty('--vh', `${vh}px`);
    };
    const handleOrientationChange = () => {
      setTimeout(setViewportHeight, 100);
    };
    setViewportHeight();
    window.addEventListener('resize', setViewportHeight);
    window.addEventListener('orientationchange', handleOrientationChange);

    this.destroyRef.onDestroy(() => {
      window.removeEventListener('resize', setViewportHeight);
      window.removeEventListener('orientationchange', handleOrientationChange);
    });
  }

  private setupRouteHandler(): void {
    this.router.events
      .pipe(
        filter(
          (event): event is NavigationEnd => event instanceof NavigationEnd,
        ),
        takeUntilDestroyed(this.destroyRef),
      )
      .subscribe((event) => {
        if (this.isFlutterRoute()) {
          const fullRoute = event.urlAfterRedirects;
          const [path, queryString] = fullRoute.split('?');
          const cleanPath = path.replaceAll(/(^\/+)|(\/+$)/g, '');
          const queryParams = queryString
            ? Object.fromEntries(new URLSearchParams(queryString))
            : undefined;

          this.broadcastService.sendAction(ActionType.navigate, {
            route: cleanPath,
            queryParams,
            timestamp: Date.now(),
          });
        }
      });
  }

  ngOnDestroy(): void {
    this.clientDataService.clearSelectedClientSignal();
    this.authDataService.clearAllUserData();
  }
}
