import { ErrorHandler, inject, Injectable } from '@angular/core';
import { ToastService } from './toast.service';

@Injectable({
  providedIn: 'root',
})
export class GlobalErrorHandlerService implements ErrorHandler {
  private readonly toastService = inject(ToastService);
  private hasShownChunkError = false;

  handleError(error: Error): void {
    console.error('Global error:', error);

    // Detect chunk loading errors
    if (this.isChunkLoadError(error)) {
      this.handleChunkLoadError();
      return;
    }

    // Handle other errors normally
    console.error('Unhandled error:', error);
  }

  private isChunkLoadError(error: Error): boolean {
    const chunkFailedMessage = /Loading chunk \d+ failed/;
    const dynamicImportFailed = /Failed to fetch dynamically imported module/;
    const chunkLoadError = error.name === 'ChunkLoadError';
    const messageMatch =
      chunkFailedMessage.test(error.message) ||
      dynamicImportFailed.test(error.message);

    return chunkLoadError || messageMatch;
  }

  private handleChunkLoadError(): void {
    // Prevent multiple reload prompts
    if (this.hasShownChunkError) {
      return;
    }

    this.hasShownChunkError = true;

    this.toastService.showWarning(
      'Update Available',
      'A new version is available. The page will reload to get the latest version.',
      { life: 5000 },
    );

    // Reload after showing toast
    setTimeout(() => {
      globalThis.location.reload();
    }, 2000);
  }
}
