import { HttpErrorResponse, HttpInterceptorFn } from '@angular/common/http';
import { inject } from '@angular/core';
import { throwError } from 'rxjs';
import { ApiUrlService } from '../services/api-url.service';
import { ToastService } from '../services/toast.service';
import { ErrorReasons } from '../models/error-reasons.enum';
import { isDefined } from '../../shared/utils';
import { Configuration } from '../../api';

export const ApiUrlInterceptor: HttpInterceptorFn = (req, next) => {
  const apiUrlService = inject(ApiUrlService);
  const toastService = inject(ToastService);
  const apiConfiguration = inject(Configuration);

  // --- Get and validate the DYNAMIC base URL ---
  let dynamicApiUrlObj: URL;
  try {
    const currentApiUrl = apiUrlService.getCurrentApiUrl();
    if (!currentApiUrl) {
      throw new Error('API URL service returned an empty URL.');
    }
    dynamicApiUrlObj = new URL(currentApiUrl);
  } catch (error) {
    const errorMsg = `Invalid dynamic API URL configured: ${(error as Error)?.message ?? 'Unknown error'}. Please check settings.`;
    const errorResponse = new HttpErrorResponse({
      status: 400,
      statusText: 'Bad Request',
      error: {
        errorReason: ErrorReasons.InvalidEndpoint,
        errorMessage: errorMsg,
        token: null,
        valid: false,
      },
    });
    toastService.showError('Configuration Error', errorMsg);
    return throwError(() => errorResponse);
  }

  // --- Normalize the DYNAMIC base URL (ensure it ends with /) ---
  let dynamicApiBaseUrl = dynamicApiUrlObj.toString();
  if (!dynamicApiBaseUrl.endsWith('/')) {
    dynamicApiBaseUrl += '/';
  }

  // --- Check if the request URL needs modification ---
  // Only modify requests that start with 'http' AND likely used the configured basePath
  if (req.url.startsWith('http')) {
    // --- Get the ORIGINAL basePath configured in ApiModule ---
    const originalBasePath = apiConfiguration.basePath; // Get the basePath used at config time

    if (isDefined(originalBasePath) && req.url.startsWith(originalBasePath)) {
      // --- Extract the endpoint path RELATIVE to the ORIGINAL basePath ---
      // Example: req.url = "https://default.com/v1/users", originalBasePath = "https://default.com/v1"
      // We want "/users"
      let endpointPath = req.url.substring(originalBasePath.length);

      // Remove leading slash if present, as dynamicApiBaseUrl ends with one
      if (endpointPath.startsWith('/')) {
        endpointPath = endpointPath.substring(1);
      }

      // --- Construct the final URL ---
      const finalUrl = `${dynamicApiBaseUrl}${endpointPath}`;
      const modifiedRequest = req.clone({ url: finalUrl });
      return next(modifiedRequest);
    } else {
      // The request URL starts with http, but NOT with the configured originalBasePath.
      // This could be a request to a different domain or an asset.
      // OR originalBasePath was not set.
      // We *could* still try and replace the origin, but it's safer to leave it
      // if it doesn't match the expected pattern.

      return next(req);
    }
  }

  // --- Pass through requests that don't start with 'http' (e.g., local assets) ---
  return next(req);
};
