import { inject } from '@angular/core';
import { firstValueFrom } from 'rxjs';
import { AuthService as CoreAuthService } from '../services/auth.service';
import { AuthService as ApiAuthService, UserProfileResponse } from '../../api';
import { AuthDataService } from '../services/auth-data.service';
import { ClientDataService } from '../../features/clients/services/client-data.service';
import { LocalStorageService } from '../../shared/services/local-storage.service';
import { isDefined, isNullOrUndefined } from '../../shared/utils';
import { UserSessionService } from '../services/user-session.service';
import { SessionCleanupService } from '../services/session-cleanup.service';

/**
 * Application startup initializer.
 *
 * CRITICAL: This runs BEFORE routing and BEFORE guards.
 * It ensures user profile and sessions are loaded before navigation begins.
 *
 * Responsibilities:
 * 1. Check if user is authenticated
 * 2. If authenticated, load user profile
 * 3. Set user ID in LocalStorageService for correct namespacing
 * 4. Detect and handle user mismatches
 * 5. Force UserSessionService to reload sessions with correct namespace
 * 6. Restore selected client if available
 */
export function startupInitializer(): () => Promise<void> {
  return async () => {
    const coreAuth = inject(CoreAuthService);
    const apiAuth = inject(ApiAuthService);
    const authData = inject(AuthDataService);
    const clientData = inject(ClientDataService);
    const localStorage = inject(LocalStorageService);
    const userSessionService = inject(UserSessionService);
    const sessionCleanup = inject(SessionCleanupService);

    // Fast path: not authenticated -> nothing to preload.
    if (!coreAuth.isAuthenticated()) {
      return;
    }

    try {
      // 1. Load user profile if not already present
      let profile: UserProfileResponse | null = authData.getUserProfile();
      if (!profile) {
        profile = await firstValueFrom(apiAuth.getUserProfile());
        if (isDefined(profile)) {
          await authData.setUserProfile(profile);
        }
      }

      if (isNullOrUndefined(profile)) {
        return;
      }

      // 2. Check for user mismatch ONLY if this is a fresh login (not account switch)
      // We can detect account switch by checking if sessions already exist
      const hasExistingSessions = userSessionService.userSessions().length > 0;

      if (isDefined(profile.id) && !hasExistingSessions) {
        // Only check mismatch on fresh login, not on app reload after account switch
        const hasMismatch = sessionCleanup.detectAndHandleUserMismatch(
          profile.id,
        );
        if (hasMismatch) {
          console.warn(
            '[Startup] User mismatch detected, previous user data cleared',
          );
        }

        // Migrate any pending data to actual user namespace
        localStorage.migrateFromPendingToUser(profile.id);

        // Update last authenticated user
        sessionCleanup.setLastAuthenticatedUser(profile.id);
      } else if (isDefined(profile.id)) {
        // On reload, just update the last authenticated user (no mismatch check)
        sessionCleanup.setLastAuthenticatedUser(profile.id);
      }

      // 3. CRITICAL: Reload user sessions from storage now that we have the correct user namespace
      // This ensures sessions are available BEFORE guards run

      userSessionService.reloadSessionsFromStorage();

      clientData.markInitializationComplete();
    } catch {
      clientData.markInitializationComplete();
    }
  };
}
