import { inject } from '@angular/core';
import { CanActivateFn, Router, UrlTree } from '@angular/router';
import { UserSessionService } from '../services/user-session.service';
import { isDefined } from '../../shared/utils';
import { AuthService } from '../services/auth.service';

export const clientSelectedGuard: CanActivateFn = (): boolean | UrlTree => {
  const userSessionService = inject(UserSessionService);
  const authService = inject(AuthService);
  const router = inject(Router);

  // First check if user is authenticated
  if (!authService.isAuthenticated()) {
    return router.createUrlTree(['/auth', 'login']);
  }

  const hasSelectedSession = isDefined(userSessionService.currentUserSession());
  const hasSessions = userSessionService.userSessions().length > 0;

  if (hasSelectedSession) {
    return true; // User has a selected client, allow access.
  }

  // User is authenticated but has not selected a client.
  if (hasSessions) {
    // Redirect them to the selection page.
    return router.createUrlTree(['/auth', 'select-client']);
  }

  // No sessions available - this shouldn't happen, but redirect to login as fallback
  console.warn(
    '[ClientSelectedGuard] User authenticated but has no available sessions, redirecting to login',
  );
  return router.createUrlTree(['/auth', 'login']);
};
