import {
  ChangeDetectionStrategy,
  Component,
  computed,
  inject,
} from '@angular/core';
import { toSignal } from '@angular/core/rxjs-interop';

import { DomSanitizer } from '@angular/platform-browser';
import { ActivatedRoute } from '@angular/router';
import { NavigationService } from '../../core/services/navigation.service';
import { isDefined } from '../../shared/utils';

@Component({
  selector: 'app-external-tab',
  templateUrl: './external-tab.component.html',
  styleUrls: ['./external-tab.component.scss'],
  standalone: true,
  imports: [],
  changeDetection: ChangeDetectionStrategy.OnPush,
})
export class ExternalTabComponent {
  private readonly route = inject(ActivatedRoute);
  private readonly sanitizer = inject(DomSanitizer);
  private readonly navigationService = inject(NavigationService);

  private readonly params = toSignal(this.route.paramMap);

  readonly iframeUrl = computed(() => {
    const id = this.params()?.get('id');
    if (id === null || id === undefined) {
      return null;
    }
    const navItem = this.navigationService.getNavigationItemById(id);
    if (isDefined(navItem?.externalUrl) && navItem.externalUrl.trim() !== '') {
      return this.sanitizer.bypassSecurityTrustResourceUrl(navItem.externalUrl);
    }
    return null;
  });
}
