import { Injectable, computed, signal, inject } from '@angular/core';
import { Router, NavigationEnd } from '@angular/router';
import { BehaviorSubject, filter } from 'rxjs';
import { takeUntilDestroyed } from '@angular/core/rxjs-interop';
import { ClientDataService } from '../../features/clients/services/client-data.service';
import { AuthDataService } from './auth-data.service';
import { ApiSchemaService } from './api-schema.service';
import { IconDefinition } from '@fortawesome/fontawesome-svg-core';
import { AppFeatureDefaultsStructure } from '../models/feature-flags.model';
import { isDefined, isEmpty } from '../../shared/utils';

export interface NavigationGroup {
  id: string;
  name: string;
  items: NavigationItem[];
}

export interface PageDefinition {
  name: string | ((features: AppFeatureDefaultsStructure) => string);
  route?: string;
  featureFlag?: string | string[];
  permissions?: string | string[];
  id?: string;
  externalUrl?: string;
  icon?:
    | IconDefinition
    | string
    | ((features: AppFeatureDefaultsStructure) => IconDefinition | string);
  visible?: (
    features: AppFeatureDefaultsStructure,
    isMobile?: boolean,
  ) => boolean;
  group?: string | ((features: AppFeatureDefaultsStructure) => string);
  order?: number | ((features: AppFeatureDefaultsStructure) => number);
  children?: Record<string, PageDefinition>;
  getChildren?: (
    features: AppFeatureDefaultsStructure,
  ) => Record<string, PageDefinition>;
  external?: boolean;
  iframe?: boolean;
}

export interface NavigationItem {
  id: string;
  external: boolean;
  iframe?: boolean;
  icon?: string | IconDefinition;
  name: string;
  group: string;
  order: number;
  route?: string;
  externalUrl?: string;
  permissions?: string | string[];
  children?: NavigationItem[];
  featureFlag?: string | string[];
}

