  private readonly activeRoot = new BehaviorSubject<string>('overview');
  readonly activeRoot$ = this.activeRoot.asObservable();

  readonly pageHistory = signal<Record<string, string>>({});
  readonly urlHistory = signal<string[]>([]);
  readonly isMobile = signal<boolean>(false);

  // Computed signals for reactive navigation
  readonly navigationItems = computed(() => this.computeNavigationItems());
  readonly navigationGroups = computed(() => this.computeNavigationGroups());

  readonly previousUrl = computed(() => {
    const history = this.urlHistory();
    return history.at(-2) ?? '';
  });

  constructor() {
    // Subscribe to router events
    this.router.events
      .pipe(
        filter((event) => event instanceof NavigationEnd),
        takeUntilDestroyed(),
      )
      .subscribe((event: NavigationEnd) => {
        if (!event.urlAfterRedirects.includes('redirecting=')) {
          this.urlHistory.update((history) => [
            ...history.slice(-19), // Keep last 20 URLs
            event.urlAfterRedirects,
          ]);
        }
        this.setPath(event.urlAfterRedirects || event.url);
      });

    // Set initial path
    this.setPath(this.router.url);

    // Listen to window resize for mobile detection
    this.checkMobileState();
    window.addEventListener('resize', () => this.checkMobileState());
  }

  private checkMobileState(): void {
    this.isMobile.set(window.innerWidth < 768); // Adjust breakpoint as needed
  }
  private setPath(url: string): void {
    const pages = this.getPages();
    const root = Object.keys(pages).find((x) => url.indexOf(x) === 1);

    if (root != null) {
      const page = pages[root];
      this.pageHistory.update((history) => ({ ...history, [root]: url }));

      const children =
        page.children ??
        (page.getChildren ? page.getChildren(this.getFeatures()) : {});
      const child = Object.keys(children).find((x) => url.includes(x));

      this.activeRoot.next(isDefined(child) ? `${root}/${child}` : root);
    }
  }
