import { CommonModule } from '@angular/common';
import { Component, inject, output, signal } from '@angular/core';
import { ButtonModule } from 'primeng/button';
import { DialogModule } from 'primeng/dialog';
import { ClientSelectorComponent } from '../../../../shared/components/client-selector/client-selector.component';
import { AuthTokenManagerService } from '../../../../core/services/auth-token-manager.service';
import { UserSessionService } from '../../../../core/services/user-session.service';
import { AuthService } from '../../../../api';
import { firstValueFrom } from 'rxjs';
import { Router } from '@angular/router';
import { ClientDataService } from '../../../clients/services/client-data.service';
import { AuthDataService } from '../../../../core/services/auth-data.service';
import { ProgressSpinnerModule } from 'primeng/progressspinner';
import { isDefined } from '../../../../shared/utils';
import { SessionCleanupService } from '../../../../core/services/session-cleanup.service';

@Component({
  selector: 'app-account-switcher',
  imports: [
    DialogModule,
    ClientSelectorComponent,
    CommonModule,
    ButtonModule,
    ProgressSpinnerModule,
  ],
  templateUrl: './account-switcher.component.html',
  styleUrl: './account-switcher.component.scss',
})
export class AccountSwitcherComponent {
  readonly closeModal = output<void>();
  private readonly userSessionService = inject(UserSessionService);
  private readonly apiAuthService = inject(AuthService);
  private readonly authTokenManager = inject(AuthTokenManagerService);
  private readonly router = inject(Router);
  private readonly clientDataService = inject(ClientDataService);
  private readonly authDataService = inject(AuthDataService);
  private readonly sessionCleanup = inject(SessionCleanupService);

  accounts = this.userSessionService.userSessions();
  loading = signal(false);
  display = signal<boolean>(true);

  closeDialog(): void {
    this.display.set(false);
    this.closeModal.emit();
  }

  async onAccountSelected(accountId: string): Promise<void> {
    this.loading.set(true);

    try {
      const response = await firstValueFrom(
        this.apiAuthService.selectUser(accountId),
      );

      // Store new tokens
      this.authTokenManager.storeTokens({
        accessToken: response.accessToken,
        refreshToken: response.refreshToken,
        idToken: response.idToken ?? '',
        users: this.accounts,
      });

      // Update current session (this stores in localStorage)
      this.userSessionService.setCurrentUserSession(accountId);

      // CRITICAL: Update last authenticated user BEFORE reload
      // This prevents user mismatch detection from triggering on reload
      const selectedSession = this.accounts.find((s) => s.id === accountId);
      if (isDefined(selectedSession?.id)) {
        this.sessionCleanup.setLastAuthenticatedUser(selectedSession.id);
      }

      // Clear the old profile so startupInitializer fetches the new one
      this.authDataService.clearUserProfile();

      // Optional: Clear client data to avoid stale data flash
      this.clientDataService.clearSelectedClient();

      this.loading.set(false);
      this.closeDialog();

      // Navigate to safe route then reload
      await this.router.navigate(['/position']);

      // Small delay to ensure navigation completes
      setTimeout(() => {
        globalThis.location.reload();
      }, 100);
    } catch (error) {
      console.error('Error selecting user:', error);
      this.loading.set(false);
    }
  }
}
