import { inject } from '@angular/core';
import { ResolveFn } from '@angular/router';
import { AuthService as ApiAuthService, UserProfileResponse } from '../../api';
import { AuthDataService } from '../services/auth-data.service';
import { SessionCleanupService } from '../services/session-cleanup.service';
import { LocalStorageService } from '../../shared/services/local-storage.service';
import { UserSessionService } from '../services/user-session.service';
import { of, switchMap, from } from 'rxjs';
import { isDefined } from '../../shared/utils';

export const userProfileResolver: ResolveFn<
  UserProfileResponse | null
> = () => {
  const apiAuth = inject(ApiAuthService);
  const authData = inject(AuthDataService);
  const sessionCleanup = inject(SessionCleanupService);
  const localStorage = inject(LocalStorageService);
  const userSessionService = inject(UserSessionService);

  const existing = authData.getUserProfile();
  if (existing) {
    return of(existing);
  }

  return apiAuth.getUserProfile().pipe(
    switchMap((profile) => {
      if (profile == null) {
        return of(profile);
      }

      return from(
        (async () => {
          if (isDefined(profile.id)) {
            // Update last authenticated user
            sessionCleanup.setLastAuthenticatedUser(profile.id);

            // Migrate any pending data to actual user namespace
            localStorage.migrateFromPendingToUser(profile.id);
          }

          await authData.setUserProfile(profile);

          // Reload sessions now that profile is set and namespace is correct
          userSessionService.reloadSessionsFromStorage();

          return profile;
        })(),
      );
    }),
  );
};
