import { inject } from '@angular/core';
import {
  ActivatedRouteSnapshot,
  CanActivateFn,
  Router,
  RouterStateSnapshot,
} from '@angular/router';
import { get } from 'lodash';
import { ClientDataService } from '../../features/clients/services/client-data.service';
import { isNullOrUndefined } from '../../shared/utils';

type FeatureFlagPath = string | string[];

/**
 * Guard that checks a feature flag path on the client features object.
 * Usage in route data:
 *   data: { featureFlag: 'page.alerts.enabled' }
 * or multiple:
 *   data: { featureFlag: ['page.alerts.enabled', 'page.videos.enabled'], requireAll: false }
 */
function isAllowedByFlags(
  features: unknown,
  flag: FeatureFlagPath | undefined,
  requireAll: boolean,
): boolean {
  const noFlagSpecified =
    isNullOrUndefined(flag) || (Array.isArray(flag) && flag.length === 0);
  if (noFlagSpecified) return true;

  const evaluate = (path: string): boolean =>
    Boolean(get(features as Record<string, unknown>, path));

  return Array.isArray(flag)
    ? requireAll
      ? flag.every(evaluate)
      : flag.some(evaluate)
    : evaluate(flag);
}

export const featureGuard: CanActivateFn = (
  route: ActivatedRouteSnapshot,
  _state: RouterStateSnapshot,
) => {
  const router = inject(Router);
  const clientDataService = inject(ClientDataService);
  const flag = route.data?.['featureFlag'] as FeatureFlagPath | undefined;
  const requireAll =
    (route.data?.['requireAll'] as boolean | undefined) ?? true;

  // If no flag provided allow immediately
  if (isNullOrUndefined(flag) || (Array.isArray(flag) && flag.length === 0)) {
    return true;
  }

  const evaluate = () => {
    const features = clientDataService.getFeaturesSignal()();
    const allowed = isAllowedByFlags(features, flag, requireAll);
    return allowed ? true : router.createUrlTree(['']);
  };

  if (clientDataService.isFeatureEvaluationReady()) {
    return evaluate();
  }

  return clientDataService
    .waitForFeatureReadiness()
    .then(() => evaluate())
    .catch((err) => {
      console.warn(
        '[featureGuard] waitForFeatureReadiness failed; proceeding with current features.',
        err,
      );
      return evaluate();
    });
};
